.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 54px;
}


.title {
    margin: 6px auto 120px auto;
    font-weight: 600;
    font-size: 50px;
    color: #054A29;
    line-height: 60px;
    padding: 0 16px 6px 16px;
    border-bottom: 2px dashed #054A29;
}


@media screen and (max-width: 497px) {
    .title {
        padding: 0 16px 16px 16px;
    }
}